import styled from "styled-components"
import moonImg from "../images/moon.png"
import treeImg from "../images/tree.png"
import tree2Img from "../images/tree2.png"
import seaImg from "../images/sea.png"
import { mobile } from "../responsive"
import { Animated } from "react-animated-css"
import { AnimationOnScroll } from "react-animation-on-scroll"

const Container = styled.div`
  z-index: -2;
`
const Moon = styled.img`
  width: 100vw;
  transform: translate(-7vw, 0vh);
  ${mobile({ width: "200vw" })};
`
const Tree = styled.img`
  position: relative;
  width: 50vw;
  transform: translate(-5vw, 0vh);
  ${mobile({ width: "200vw", transform: "translate(-80vw, 0vh)" })}
`
const Tree2 = styled.img`
  position: relative;
  z-index: 1;
  width: 50vw;
  transform: translate(10vw, 0);
  ${mobile({ width: "200vw", transform: "translate(10vw, 30vh)" })}
`
const Sea = styled.img`
  position: absolute;
  z-index: 1;
  width: 100vw;
  bottom: 0;
  left: 0;
`
Moon.defaultProps = {
  src: moonImg,
}

Tree.defaultProps = {
  src: treeImg,
}

Tree2.defaultProps = {
  src: tree2Img,
}

Sea.defaultProps = {
  src: seaImg,
}

const background = () => {
  const animatedStyle = {
    backgroundColor: "transparent",
  }
  return (
    <Container>
      <Animated
        animationIn='fadeIn'
        animationOut='fadeOut'
        isVisible={true}
        style={animatedStyle}
      >
        <Moon />
      </Animated>
      <AnimationOnScroll
        animateIn='fadeInUp'
        style={animatedStyle}
        animateOnce={true}
      >
        {" "}
        <Tree />
        <Tree2 />
      </AnimationOnScroll>
      <AnimationOnScroll
        animateIn='fadeIn'
        style={animatedStyle}
        animateOnce={true}
      >
        <Sea />
      </AnimationOnScroll>
    </Container>
  )
}

export default background
